<template>
	<view class="item_body">
		<view class="item_body_top">
			<image :src="img"
			 mode=""></image>
		</view>
		<view class="item_body_info">
			<view class="item_body_title">{{title}}</view>
			<view class="item_body_coordinates">
				<view>
					<text style="margin: 0 10upx;">{{distance}}km</text>|
					<text style="margin: 0 10upx;">{{place}}</text>
				</view>
			</view>
			<view class="price_info">
				<view style="color: #F11B09;"><text class="fts10">¥</text>{{price}}.<text class="fts10">00</text></view>
				<view class="fts10" style="color: #d3d3d3;">{{want}}人想要</view>
			</view>
			<view style="width: 100%;height: 3upx; background-color: #EEEEEE;"></view>
			<view class="item_body_user">
				<view class="item_body_user_img">
					<image :src="user" mode=""></image>
				</view>
				<view style="font-size: 15px;">{{nickname}}</view>
			</view>
		</view>
	</view>

</template>

<script>
	export default {
		name: 'SecondPro',
		props: [
			'title',
			'distance',
			'img',
			'place',
			'price',
			'want',
			'user',
			'nickname'
		]
	}
</script>

<style>
	.item_body_box {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	.item_body {
		/* width: 48%; */
		/* min-height: 50upx; */
		margin-bottom: 15upx;
		overflow: hidden;
		border-radius: 5px;
		background-color: #FFFFFF;
		box-shadow: 5px 5px 5px #EEEEEE;
	}

	.item_body_top {
		height: 200upx;
	}

	.item_body_top>image {
		width: 100%;
		height: 100%;
	}

	.item_body_info {
		padding: 5upx 10upx;
	}

	.item_body_title {
		font-size: 13px;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		line-height: 1.5;
	}

	.item_body_coordinates {
		display: flex;
		justify-content: start;
		font-size: 10px;
		color: #AAAAAA;
		align-items: center;
		margin: 7upx 0;
	}

	.item_body_coordinates>image {
		width: 15px;
		height: 15px;
	}

	.price_info {
		display: flex;
		justify-content: space-between;
		font-size: 14px;
		margin: 10upx 0;
	}

	.item_body_user {
		display: flex;
		align-items: center;
		justify-content: start;
		margin: 10upx 0;
	}

	.item_body_user_img {
		margin-right: 20upx;
	}

	.item_body_user>.item_body_user_img>image {
		width: 50upx;
		height: 50upx;
		border-radius: 50%;
	}

	.fts10 {
		font-size: 10px;
	}
</style>
